body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  font-family: 'Lora', serif;
  margin: 0;
  padding: 0;
}
.changetheme {
  float: left;


}
.changetheme img{
  width: 40px;
  height: 40px;
  position: fixed;
  padding: 10px;
}




.navbar {
    overflow: hidden;
    background-color: #004080;
    position: fixed;
    top: 0;
    width: 100%;

}

/* Links inside the navbar */
.navbar a {
    float: right;
    display: block;
    color: #872f2f;
    text-align: center;
    padding: 7px 8px;
    text-decoration: none;
    font-size: xx-large;
    padding: 10px;
}

a:hover{
    color: red;
}

.fullpage{
    width: 99.1vw;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex: auto;
            flex: auto;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.profile{
    border-radius: 100px;
}
.first{
    background-color: #c3d2e0;
}
.icon {
    margin: 5px;
}
.icon-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.second{
    font-size: x-large;
    background-color:#282c34;
    color: white;
}
.aboutText{
    text-align: center;
    margin: 1% 30%;
}
.third{
    background-color: #c3c8cc;

}

.cards-wrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
}
.skillsTitle{
    font-size: xx-large;
}

.card {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: center;
            justify-content: center;
    height: 200px;
    width: 120px;
    background-color: #282c34;
    margin: 10px;
    padding: 20px;
    border-radius: 10px;    box-shadow: 0 1px 3px rgba(0, 0, 0, 12), 0 1px 2px rgba(0, 0, 0, 12);
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}
.card:hover{
    box-shadow: 0 14px 28px rgba(0, 0, 0, 25), 0 10px 10px rgba(0, 0, 0, 25);
}

.image-wrapper {
    width: 110px;
    height: 90px;
    text-align: center;
    padding: 5px;
}
img {
    max-width: 100%;
    max-height: 100%;
}
.skill-title-wrapper{
    color: white;
    text-align: center;
    position: relative;
    right: 10%;
    left: 10%;
    width: 80%;
    height: 30%;
    padding: 0;
    margin: 0;

}
